/***/ (function(module, exports) {

	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"You May Be Right","date":"18 Sep 2002","slug":"/2002-09-18-you-may-be-right/","tags":"Y2002"},"html":"<p>I noticed that Brent's weblogs <a href=\"http://inessential.com/\">inessential.com</a> and <a href=\"http://ranchero.com/\">ranchero.com</a> weren't showing up on my blogroll, and asked Brent whether he was still pinging weblogs.com. He replied that he's not (inadvertantly), and that weblogs.com may have outlived its usefulness anyway.</p>\n<p>To paraphrase William Joel, 'you may be right'.  I'm finding a number of sites that I'd like to follow don't ping or ping irregularly, and that a number of pings are false alarms. But is maintaining local copies of 50 RSS feeds over dialup really a better alternative?</p>\n<p>With all due respect to <a href=\"http://ranchero.com/software/netnewswire/\">desktop RSS readers</a>, I have too many desktops for that to be practical. The only thing my desktops have in common is Mozilla. For me it makes more sense to keep the feeds I want to follow at my hosting server. It doesn't bother me to have my feeds publicly accessible. I <em>want</em> people to know what I read. I don't want an <a href=\"http://www.disobey.com/amphetadesk/\">Amphetadesk</a>, I want an Amphetaserver!</p>\n<p><a href=\"http://traumwind.tierpfad.de/blog/\">Martin</a> has put together an <a href=\"http://traumwind.server-wg.de/blog/magpie/index.php?url=http://traumwind.tierpfad.de/blog/rss.php\">RSS reader with PHP</a>, and that might be the direction I'll go.</p>"},"next":{"fields":{"title":"Uninspiring","date":"18 Sep 2002","slug":"/2002-09-18-uninspiring/"}},"previous":{"fields":{"title":"Who's Guarding the Asylum?","date":"19 Sep 2002","slug":"/2002-09-19-whos-guarding-the-asylum/"}}},{"node":{"fields":{"title":"Uninspiring","date":"18 Sep 2002","slug":"/2002-09-18-uninspiring/","tags":"Y2002"},"html":"<p>Yesterday was totally uninspired. Whatever I tried at work backfired, I was going backwards instead of making progress. Mama's out of town, and Christopher was wary and antsy. After I left him in bed bed he got up again and fell on his head. He said his ear hurt badly, and he is not prone to simulate such things. I could see no external signs of injury, but I still had visions of a concussion or worse. A cold pack seemed to help, but it then took another 2 hours to get him to sleep. </p>\n<p>I've started adding RSS links to the <a href=\"http://www.papascott.de/sidebar.php\">papascott blogroll</a>, by hand for now, being uninspired. I will be doing something more intelligent with them,  I promise.</p>"},"next":{"fields":{"title":"Trading Allies for Votes","date":"16 Sep 2002","slug":"/2002-09-16-trading-allies-for-votes/"}},"previous":{"fields":{"title":"You May Be Right","date":"18 Sep 2002","slug":"/2002-09-18-you-may-be-right/"}}},{"node":{"fields":{"title":"Trading Allies for Votes","date":"16 Sep 2002","slug":"/2002-09-16-trading-allies-for-votes/","tags":"Y2002"},"html":"<p>One week before election day, Schröder has pulled ahead of Stoiber. As usual, the NY Times gets the analysis mostly right in <a href=\"http://www.nytimes.com/2002/09/15/international/europe/15GERM.html\">For Now, Trading Allies for Votes</a>. They forget that Bush and Schröder got off to a rocky start, as the Germans were blind sided a number of times in the opening weeks of the Bush administration.  In fact, Schröder is not taking that much of a foreign policy risk by playing the Iraq card, since Bush doesn't seem to be listening to the Europeans anyway, and wouldn't have asked for German troops in any case. Not being on the Security Council, German support or opposition makes little difference. When the war does come, Schröder has left himself a rhetorical out, albeit a thin one, if Iraq can be linked to terrorism (and it will be). But Germany can forget about a permanent UN Security Council seat for a while.</p>"},"next":{"fields":{"title":"New Home For Sunlog","date":"14 Sep 2002","slug":"/2002-09-14-new-home-for-sunlog/"}},"previous":{"fields":{"title":"Uninspiring","date":"18 Sep 2002","slug":"/2002-09-18-uninspiring/"}}}],"pathPrefix":"","first":false,"last":false,"index":846,"pageCount":1101}}

/***/ })

